import React, { Suspense, lazy } from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Preloader from "./components/preloader";
// import CreateProduct from "./pages/product/create";

const CreateProduct = lazy(()=>import('./pages/product/create'))
const SignIn = lazy(() => import("./pages/signin"));
const SignUp = lazy(() => import("./pages/signup"));
const ErrorNotFound = lazy(() => import("./pages/error-404"));
const Error500 = lazy(() => import("./pages/error-500"));
const Error503 = lazy(() => import("./pages/error-503"));
const Error505 = lazy(() => import("./pages/error-505"));
const ProfileEdit = lazy(() => import("./pages/profile/edit"));
const ProductList = lazy(()=>import('./pages/product/list'))
const OneProduct = lazy(() => import('./pages/product/item/one'))
const DashboardTwo = lazy(() => import('./pages/dashboard-two'))

const App: React.FC = () => {
    return (
        <>
            <Router>
                <Suspense fallback={<Preloader />}>
                    <Switch>
                        <Route exact path="/" component={SignIn} />
                        <Route exact path="/signup" component={SignUp} />
                        <Route exact path="/profile/edit" component={ProfileEdit} />
                        <Route exact path="/product/list" component={ProductList} />
                        <Route exact path="/product/create" component={CreateProduct} />
                        <Route exact path="/product/:id" component={OneProduct} />
                        <Route exact path="/dashboard" component={DashboardTwo} />
                        <Route exact path="/error-500" component={Error500} />
                        <Route exact path="/error-503" component={Error503} />
                        <Route exact path="/error-505" component={Error505} />
                        <Route path="*" component={ErrorNotFound} />

                        {/*<Route*/}
                        {/*    exact*/}
                        {/*    path="/dashboard-three"*/}
                        {/*    component={DashboardThree}*/}
                        {/*/>*/}
                        {/*<Route*/}
                        {/*    exact*/}
                        {/*    path="/dashboard-four"*/}
                        {/*    component={DashboardFour}*/}
                        {/*/>*/}

                        {/*<Route exact path="/signup" component={SignUp} />*/}
                        {/*<Route*/}
                        {/*    exact*/}
                        {/*    path="/verify-account"*/}
                        {/*    component={VerifyAccount}*/}
                        {/*/>*/}
                        {/*<Route*/}
                        {/*    exact*/}
                        {/*    path="/forgot-password"*/}
                        {/*    component={ForgotPassword}*/}
                        {/*/>*/}
                        {/*<Route exact path="/error-500" component={Error500} />*/}
                        {/*<Route exact path="/error-503" component={Error503} />*/}
                        {/*<Route exact path="/error-505" component={Error505} />*/}
                        {/*<Route*/}
                        {/*    exact*/}
                        {/*    path="/profile-view"*/}
                        {/*    component={ProfileView}*/}
                        {/*/>*/}
                        {/*<Route*/}
                        {/*    exact*/}
                        {/*    path="/connections"*/}
                        {/*    component={Connections}*/}
                        {/*/>*/}
                        {/*<Route exact path="/groups" component={Groups} />*/}
                        {/*<Route exact path="/events" component={Events} />*/}
                        {/*<Route exact path="/timeline" component={Timeline} />*/}
                        {/*<Route exact path="/pricing" component={Pricing} />*/}
                        {/*<Route*/}
                        {/*    exact*/}
                        {/*    path="/help-center"*/}
                        {/*    component={HelpCenter}*/}
                        {/*/>*/}
                        {/*<Route exact path="/invoice" component={Invoice} />*/}
                        {/*<Route*/}
                        {/*    exact*/}
                        {/*    path="/apps/calendar"*/}
                        {/*    component={Calendar}*/}
                        {/*/>*/}
                        {/*<Route exact path="/apps/chat" component={Chat} />*/}
                        {/*<Route*/}
                        {/*    exact*/}
                        {/*    path="/apps/contacts"*/}
                        {/*    component={Contacts}*/}
                        {/*/>*/}
                        {/*<Route*/}
                        {/*    exact*/}
                        {/*    path="/apps/file-manager"*/}
                        {/*    component={FileManager}*/}
                        {/*/>*/}
                        {/*<Route exact path="/apps/mail" component={Mail} />*/}

                    </Switch>
                </Suspense>
            </Router>

            {/* <SettingsCard /> */}
        </>
    );
};

export default App;
